body{
    display: flex;
    justify-content: center;
    align-items: center;

}
.Memes{
    width: 700px;
    height: 100vh;
    box-shadow: -7px -7px 10px rgba(0,0,0,0.1),
    7px 7px 10px rgba(0,0,0,0.1);
}
/***********Navbar**********/
.Navbar{
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    display: flex;
    align-items: center;
    color:white;
    padding:0px 30px;
}
.Navbar h2{
    margin-right: auto;
    padding:0px 10px;
}
/**************header************/
.form{
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap:17px;
}
.header-button{
    grid-column: 1/-1;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    border:none;
    color:white;
    font-size: 18px;
    border-radius: 5px;
    cursor:pointer;
}
.Header{
    padding:50px;
}
.header-input{
    text-indent: 10px;
    border:1px solid #D5D4D8;
    border-radius: 5px;
}

.imageContainer{
    width: 100%;
    height: 330px;
    padding:20px 10px;
    
}
.imageContainer img{
    width: 500px;
    height: 320px;
    border-radius: 10px;
    box-shadow: -10px -10px 15px rgba(0,0,0,0.2),
    10px 10px 15px rgba(0,0,0,0.2);
}
.textonimage{
    
    font-size: 35px;
    font-weight: 900;
    color:white;
    font-family: impact, sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}
.top{
    position: relative;
    top:-330px;
}
.bottom{
    position: relative;
    top:-180px;
}